<template>
    <div class="home">
        <div v-html="htmlContent" class="content"></div>
        <div class="footer"></div>
        <!-- <img src="../blogs/ffmpeg.png" alt="" width="100%" height="200px" style="width: 100%; height: 200px;"> -->
    </div>
</template>
<script>
import { Marked, Renderer } from 'marked'
import { markedHighlight } from 'marked-highlight'
import hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'
import { useBlogStore } from '@/store/blogstore'
const render = new Renderer()
//
//render
const marked = new Marked(
    markedHighlight({
        langPrefix: 'hljs language-',
        highlight(code, lang) {
            const language = hljs.getLanguage(lang) ? lang : 'plaintext';
            return hljs.highlight(code, { language }).value;
        }
    }))


const blogStore = useBlogStore()

export default {
    data() {
        return {
            htmlContent: "",
            imageBasePath: "",
            year: "",
            tags: [],
        }
    },

    watch: {
        '$route': "updateBlog"
    },


    activated() {
        const fileName = this.$route.query.fileName
        console.log(blogStore.blogList)
        if (fileName) {
            const blogInfo = blogStore.getBlogByFileName(fileName)
            this.year = blogInfo.createDate
            this.tags = blogInfo.tags ? blogInfo.tags.split(',') : []
            this.loadBlog(blogInfo)
        }
    },

    // mounted() {
    //     const fileName = this.$route.query.fileName
    //     console.log(blogStore.blogList)
    //     if (fileName) {
    //         const blogInfo = blogStore.getBlogByFileName(fileName)
    //         this.loadBlog(blogInfo)
    //     }
    // },

    methods: {
        loadBlog(blogInfo) {

            if (!blogInfo) {
                return
            }
            const blogPath = '/blogs/' + blogInfo.fileName
            const blogImgBasePath = blogInfo.imagePath

            marked.setOptions({
                renderer: render
            })

            //
            render.image = (href, title, text) => {
                return `<img src="${blogImgBasePath}/${href}" alt="${text}" style="width: 100%;">`
            }

            //rendercode
            render.code = (code, language, escaped) => {
                //  highlight.js 
                const validLanguage = hljs.getLanguage(language) ? language : 'plaintext';
                return `<pre><code class="hljs ${validLanguage}">${code}</code></pre>`;
            }

            //
            render.codespan = (code) => {
                //code
                return `<code style="color:#fd7e14">${code}</code>`
            }

            //
            render.paragraph = (text) => {
                return `<p style="font-size: 16px;">${text}</p>`
            }

            //
            render.listitem = (text) => {
                return `<li style="font-size: 16px;">${text}</li>`
            }


            this.imageBasePath = blogImgBasePath
            console.log(blogPath, blogImgBasePath)
            hljs.highlightAll()
            fetch(blogPath)
                .then(response =>
                    response.text())
                .then(text => {
                    this.htmlContent = marked.parse(text)
                })
                .catch(err => {
                    console.log("error", err)
                })
        },
        updateBlog() {
            const fileName = this.$route.query.fileName
            console.log(blogStore.blogList)
            if (fileName) {
                const blogInfo = blogStore.getBlogByFileName(fileName)
                this.year = blogInfo.createDate
                this.tags = blogInfo.tags ? blogInfo.tags.split(',') : []
                this.loadBlog(blogInfo)
            }
        }
    },
}
</script>

